import React from 'react';
import SVG from '../../assets/images/laughing.svg';
import { Link } from 'react-router-dom';

function Navbar() {
  return (
    <header className="text-gray-700 body-font">
      <div className="container mx-auto flex flex-wrap p-5 flex-col md:flex-row md:items-center">
      <Link to="/" className="flex flex-row justify-center title-font font-medium items-center text-gray-900 mb-4 md:mb-0">
            <img className="w-8 h-8" src={SVG} />
            <span className="ml-3 text-4xl text-yonnieColors-charcoal">PrankMe!</span>
      </Link>
        <nav className="md:ml-auto flex flex-wrap items-center text-2xl justify-center">
          <Link to="/" className="mr-5 hover:text-gray-900">Home</Link>
          <Link to="/phone_number/text" className="mr-5 hover:text-gray-900">Text</Link>
          <Link to="/phone_number/call" className="mr-5 hover:text-gray-900">Call</Link>
          <Link className="mr-5 hover:text-gray-900">About</Link>
        </nav>
      </div>
    </header>
  );

}

export default Navbar;