import React, { useState } from 'react';
import TempImg from '../../assets/images/temp_logo2.png';
import TextConfirmationModal from './TextConfirmationModal';
import TextExampleModal from './TextExampleModal';

export default function TextCard(props) {

  const [showExampleModal, setShowExampleModal] = useState(false);
  const [showConfirmationModal, setShowConfirmationModal] = useState(false);


  return(
    <div className="border border-2 border-yonnieColors-charcoal flex flex-col justify-center items-center w-full md:w-1/3">
    <img className="w-64 h-64" src={require('../../assets/images/' + props.image)} />
    <h1 className="font-bold text-3xl my-4">
      {props.title}
    </h1>
    <p className="text-xl my-2 text-center">
      {props.description}
    </p>
    <div className="flex flex-col md:flex-row my-2">
      <button className="bg-yonnieColors-brown hover:bg-yonnieColors-charcoal text-white font-bold my-2 md:my-0 py-2 px-4 rounded mx-0 md:mx-2" onClick={() => {setShowConfirmationModal(true)}}>
        TEXT NOW!
       </button>
       <button className="bg-yonnieColors-brown hover:bg-yonnieColors-charcoal text-white font-bold my-2 md:my-0 py-2 px-4 rounded mx-0 md:mx-2" onClick={() => {setShowExampleModal(true);
          }}>
        SEE EXAMPLE
       </button>
    </div>
    <TextConfirmationModal 
      title={props.title}
      phoneNumber={props.phoneNumber}
      showConfirmationModal={showConfirmationModal}
      setShowConfirmationModal={setShowConfirmationModal}
    />
    <TextExampleModal
        setShowExampleModal={setShowExampleModal}
        showExampleModal={showExampleModal}
      />
  </div>
  );
}