import React, { useState } from 'react';
import CallCard from './CallCard';
import callPranks from './callPranks.js';

export default function CallPage(props) {
  let phoneNumber = props.location.phoneNumber;

  const [search, setSearch] = useState('');

  function handleChange(e) {
    setSearch(e.target.value);
  }

  return (
    <section className="py-16 md:py-32">
      <div className="container mx-auto">
        <h1 className="text-6xl text-center ranchers-font">Call Prank Message </h1>
        <p className="text-2xl text-center">
          Choose from a wide variety of our available call pranks to send to your contact, or customize your own message!
      </p>
        <div class="pt-2 flex flex-row items-center text-gray-600 w-full justify-center">
          <input class="phone-start-input-border w-3/12 bg-gray-100 border border-yonnieColors-charcoal focus:outline-none focus:border-yonnieColors-magicMint text-base px-4 py-2"
            type="search" name="search" placeholder="Search" onChange={handleChange} value={search} />
          <button type="submit" class="phone-input-border border border-yonnieColors-charcoal px-4 py-2 bg-gray-300">
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" /></svg>
          </button>
        </div>
        {/* cards */}
        <div className="p-4 flex flex-row flex-wrap">
        {
          callPranks.filter((prank) => {return prank.title.toLowerCase().includes(search.toLowerCase())}).map((prank) => <CallCard title={prank.title} description={prank.description} exampleImage={prank.exampleImage} image={prank.image} phoneNumber={phoneNumber}/>)
        }
        </div>
      </div>
    </section>
  );
}