import React, { useState } from 'react';

function TextCustomModal({ title, phoneNumber, showConfirmationModal, setShowConfirmationModal}) {

  const [customMessage, setCustomMessage] = useState('');

  return (
    <>
      {showConfirmationModal ? (
        <>
          <div
            className="justify-center items-center flex overflow-hidden fixed inset-0 z-50 outline-none focus:outline-none"
          >
            <div className="relative w-full px-4 md:px-0 md:w-1/2 my-6 mx-auto">
              {/*content*/}
              <div className="border-0 rounded-lg shadow-lg relative flex flex-col w-full bg-white outline-none focus:outline-none">
                {/*header*/}
                <div className="flex items-start justify-between p-5">
                  <h3 className="text-3xl font-semibold">
                    Confirm Text Message
                  </h3>
                  <button
                    className="p-1 ml-auto bg-transparent border-0 text-black opacity-5 float-right text-3xl leading-none font-semibold outline-none focus:outline-none"
                    onClick={() => {
                      setShowConfirmationModal(false);
                    }}
                  >
                    <span className="bg-transparent text-black opacity-5 h-6 w-6 text-2xl block outline-none focus:outline-none">
                      ×
                    </span>
                  </button>
                </div>
                {/*body*/}
                <div className="relative p-4 flex-auto w-full">
                  <div class="py-16 text-2xl">
                    You will be sending a {title} message to {phoneNumber}. If this is correct, please type your custom message below and click the confirm button to send the message.                  
                  </div>
                  <textarea type="text" value={customMessage} onChange={e => setCustomMessage(e.target.value)} placeholder="Type your custom prank message here" rows="4" className="px-3 py-3 placeholder-gray-500 text-gray-700 relative bg-white bg-white rounded text-sm shadow outline-none focus:outline-none focus:shadow-outline w-full resize-none" />
                </div>
                {/*footer*/}
                <div className="flex items-center justify-end p-3">
                <button
                    className="text-red-500 background-transparent font-bold uppercase px-6 py-2 text-sm outline-none focus:outline-none mr-1 mb-0"
                    type="button"
                    style={{ transition: "all .15s ease" }}
                    onClick={() => {
                      setShowConfirmationModal(false);
                    }}
                  >
                    Close
                  </button>
                  <button
                    className="bg-yonnieColors-charcoal text-white font-bold active:bg-yonnieColors-brown uppercase px-2 py-2 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-0"
                    type="button"
                    style={{ transition: "all .15s ease" }}
                    onClick={() => {
                      setShowConfirmationModal(false);
                    }}
                  >
                    CONFIRM
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div className="opacity-25 fixed inset-0 z-40 bg-black"></div>
        </>
      ) : null}
    </>
  );
}

export default TextCustomModal;