import React from 'react';
import TestImage from '../../assets/images/temp_logo.jpg';

function TextExampleModal({ showExampleModal, setShowExampleModal}) {

  return (
    <>
      {showExampleModal ? (
        <>
          <div
            className="justify-center items-center flex overflow-hidden fixed inset-0 z-50 outline-none focus:outline-none"
          >
            <div className="relative w-full px-4 md:px-0 md:w-1/2 my-6 mx-auto">
              {/*content*/}
              <div className="border-0 rounded-lg shadow-lg relative flex flex-col w-full bg-white outline-none focus:outline-none">
                {/*header*/}
                <div className="flex items-start justify-between p-5">
                  <h3 className="text-3xl font-semibold">
                    Prank Title Example
                  </h3>
                  <button
                    className="p-1 ml-auto bg-transparent border-0 text-black opacity-5 float-right text-3xl leading-none font-semibold outline-none focus:outline-none"
                    onClick={() => {
                      setShowExampleModal(false);
                    }}
                  >
                    <span className="bg-transparent text-black opacity-5 h-6 w-6 text-2xl block outline-none focus:outline-none">
                      ×
                    </span>
                  </button>
                </div>
                {/*body*/}
                <div className="relative p-4 flex-auto w-full">
                  <div class="mb-0 pt-0">
                    <img src={TestImage} />                    
                  </div>
                </div>
                {/*footer*/}
                <div className="flex items-center justify-end p-3">
                  <button
                    className="bg-yonnieColors-charcoal text-white font-bold active:bg-yonnieColors-brown uppercase px-2 py-2 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-0"
                    type="button"
                    style={{ transition: "all .15s ease" }}
                    onClick={() => {
                      setShowExampleModal(false);
                    }}
                  >
                    CLOSE
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div className="opacity-25 fixed inset-0 z-40 bg-black"></div>
        </>
      ) : null}
    </>
  );
}

export default TextExampleModal;