import React from 'react';
import Navbar from './components/navbar/Navbar';
import { HashRouter, Route, Switch } from "react-router-dom";
import ScrollToTop from './components/shared/ScrollToTop';
import HomePage from './components/home_page/HomePage';
import './assets/index.css';
import Footer from './components/navbar/Footer';
import PhoneNumberPageText from './components/phone_number_page/PhoneNumberPageText';
import PhoneNumberPageCall from './components/phone_number_page/PhoneNumberPageCall';
import TextPage from './components/text_page/TextPage';
import CallPage from './components/call_page/CallPage';

function App() {

  return (
    <div className="syne-font">
      <HashRouter>
        <Navbar />
        <ScrollToTop />
        <Switch>
          <Route exact path='/' component={HomePage} />
          <Route exact path='/phone_number/text' component={PhoneNumberPageText} />
          <Route exact path='/phone_number/call' component={PhoneNumberPageCall} />
          <Route exact path='/text' component={TextPage} />
          <Route exact path='/call' component={CallPage} />
        </Switch>
      </HashRouter>
      <Footer />
    </div>
  );
}

export default App;
