import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './PhoneNumberPage.css';
import Patrick from '../../assets/images/patrick.gif';

export default function PhoneNumberPageCall() {

  const [phoneNumber, setPhoneNumber] = useState('');

  function handleInputChange(e) {
    setPhoneNumber(e.target.value);
  }

  return (
    <section className="w-full phone-page-size px-4">
      <div className="container mx-auto flex flex-col items-center justify-center h-full">
        <h1 className="text-2xl ranchers-font text-center md:text-4xl my-4">Please type the phone number you would like to <span className="text-yonnieColors-cornflowerBlue">call</span>.</h1>
        <img className="w-64 h-auto my-8" src={Patrick} />
        <div className="p-2 w-full md:w-1/2 my-8 flex flex-row items-center">
        <span className="p-2 test-base bg-gray-300 phone-start-input-border border border-yonnieColors-charcoal">
          +1
        </span>
          <input onChange={handleInputChange} value={phoneNumber} className="phone-input-border w-full bg-gray-100 border border-yonnieColors-charcoal focus:outline-none focus:border-yonnieColors-magicMint text-base px-4 py-2" placeholder="(832) 920-1234" type="phone" />
        </div>
        <Link  to={{pathname: '/call', phoneNumber: phoneNumber}}>
          <button className="bg-yonnieColors-magicMint hover:bg-yonnieColors-charcoal text-white font-bold py-2 px-4 rounded">
            CONTINUE
          </button>
        </Link>
      </div>
    </section>
  );
}